body {
  margin: 0;
  font-family: system-ui, sans-serif;
  background: #f9f9f9;
}

button {
  cursor: pointer;
}

.flex          { display: flex; }
.flex-1        { flex: 1; }
.min-h-screen  { min-height: 100vh; }

.w-full { width: 100%; }
.w-64  { width: 16rem; }

.p-2  { padding: .5rem; }
.p-3  { padding: .75rem; }
.p-4  { padding: 1rem; }
.p-6  { padding: 1.5rem; }

.mb-2 { margin-bottom: .5rem; }
.mt-4 { margin-top: 1rem; }

.border    { border: 1px solid #e5e7eb; }
.border-r  { border-right: 1px solid #e5e7eb; }
.rounded   { border-radius: .375rem; }
.rounded-xl{ border-radius: .75rem; }
.shadow-md { box-shadow: 0 4px 6px rgba(0,0,0,.1); }

.bg-white   { background: #fff; }
.bg-gray-50 { background: #fafafa; }
.bg-gray-100{ background: #f5f5f5; }
.bg-gray-200{ background: #e5e5e5; }
.bg-gray-300{ background: #d4d4d4; }
.bg-green-100{ background:#dcfce7; }
.bg-red-100 { background:#fee2e2; }
.bg-blue-600{ background:#2563eb; }
.bg-blue-700{ background:#1d4ed8; }
.border-green-500{ border-color:#22c55e; }
.border-red-500  { border-color:#ef4444; }

.text-lg      { font-size: 1.125rem; }
.font-bold    { font-weight: 700; }
.font-semibold{ font-weight: 600; }
.text-left    { text-align:left; }

.hover\\:bg-gray-100:hover{ background:#eee; }
.hover\\:bg-gray-200:hover{ background:#e2e2e2; }
.hover\\:bg-blue-700:hover{ background:#1d4ed8; }

.transition { transition: background .2s, border .2s; }

.max-w-3xl { max-width: 48rem; }
.mx-auto   { margin-left: auto; margin-right: auto; }
